    /* Tooltip text */
	.tooltiptext {
  
		width: 200px;
		background-color:#163948;
		color: #fff;
		text-align: center;
		padding: 5px 0;
		border-radius: 6px;
	   
		/* Position the tooltip text - see examples below! */
		position: absolute;
		z-index: 1;
		top: 50%;
		left: 50%; 
	  }
	
	 
	
	 .tooltiptext::after {
		content: "";
		position: absolute;
		top: 0%;
		right: 10%;
	   
		
		margin-left: -60px;
		margin-top: -5px;
		border-width: 5px;
		border-style: solid;
		border-color: transparent #163948 transparent transparent;
	  }
